<template>
  <div>
    <div class="d-flex justify-content-between">
      <h4 class="mb-1">Tập dữ liệu training</h4>
    </div>
    <div class="rounded my-shadow table-60-vh"> 
      <b-table-simple no-border-collapse fixed sticky-header bordered responsive striped class="rounded mb-0 text-center">
        <b-thead>
          <b-tr class="cursor-pointer">
            <b-th>ID</b-th>
            <b-th>Lớp</b-th>
            <b-th v-for="i in label" :key="i">{{i}}</b-th>
          </b-tr>
        </b-thead>
        <b-tbody>
          <b-tr v-for="datum, index in train" :key="index">
            <b-td>{{index + 1}}</b-td>
            <b-td v-for="p, id in datum" :key="p">{{id > 0 ? formatMoney(p) : p}}</b-td>
          </b-tr>
        </b-tbody>
      </b-table-simple>
    </div>


    <div class="d-flex justify-content-between mt-5">
      <h4 class="mb-1">Tập dữ liệu testing</h4>
    </div>
    <div class="rounded my-shadow table-60-vh"> 
      <b-table-simple no-border-collapse fixed sticky-header bordered responsive striped class="rounded mb-0 text-center">
        <b-thead>
          <b-tr class="cursor-pointer">
            <b-th>ID</b-th>
            <b-th>Lớp</b-th>
            <b-th v-for="i in label" :key="i">{{i}}</b-th>
          </b-tr>
        </b-thead>
        <b-tbody>
          <b-tr v-for="datum, index in test" :key="index">
            <b-td>{{index + 1}}</b-td>
            <b-td v-for="p, id in datum" :key="p">{{id > 0 ? formatMoney(p) : p}}</b-td>
          </b-tr>
        </b-tbody>
      </b-table-simple>
    </div>
  </div>
</template>

<script>

import {
  BTableSimple,
  BThead,
  BTr,
  BTh,
  BTd,
  BTbody,
  BTfoot,
  BButton,
} from "bootstrap-vue";
import Ripple from "vue-ripple-directive";
import axios from "@axios";
import { formatMoney } from '@core/utils/filter'
import FeatherIcon from '@/@core/components/feather-icon/FeatherIcon.vue';
import Dot from "@/components/Dot"


export default {
  components: {
    BTableSimple,
    BThead,
    BTr,
    BTh,
    BTd,
    BTbody,
    BTfoot,
    BButton,
    FeatherIcon, Dot
  },

  directives: {
    Ripple,
  },
  data() {
    return {
      label: [],
      train: [],
      test: []
    }
  },

  methods: {
    formatMoney
  },

  mounted() {
    let type_of_data = localStorage.getItem('type_of_data')
    if(!type_of_data) {
      this.$router.push('/')
      return
    }
    let all_label = {
      'glass': ['RI', 'Na', 'Mg', 'Al', 'Si', 'K', 'Ca', 'Ba', 'Fe'],
      'ecoli': ['mcg', 'gvh', 'aac', 'alm1', 'alm2'],
      'battery': ['Tmax', 'U20', 'U80', 'Imean', 'Imax', 'Qc_max']
    }
    this.label = all_label[type_of_data]

    axios.get(`https://hais2.herokuapp.com/data/${type_of_data}/train`).then(res => {
      this.train = res.data
    })
    axios.get(`https://hais2.herokuapp.com/data/${type_of_data}/test`).then(res => {
      this.test = res.data
    })

    // this.$store.commit('app/SET_DATA_LIVE_TIME', moment().format(' HH:mm:ss - DD/MM/YYYY'))
  },
}
</script>
